.o2
{
    width: 100%;
    max-height: 40dvh;
    height: 40dvh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
   background-color: #0f0f0f; 

}
.k2
{
height: 40dvh;
width: 100%;
max-width: 1100px;

display: flex;
align-items: center;
justify-content: space-between;
flex-direction: column;
}
.small-txt2
{
    color: transparent;
    background-image: radial-gradient(circle farthest-side at 45%, #ffffff, rgba(255, 255, 255, 0.43));
    background-clip: text;
font-size: 16px;
text-align: center;
}
.top-bottom
{
width: 100%;

height: 20dvh;
display: flex;
justify-content: space-between;
align-items: center;
flex-direction: column;
}
.top2
{
    width: 100%;
    height: 49%;

display: flex;
justify-content: space-between;
align-items: center;
}
.bottom2
{
    width: 100%;
    height: 49%;

display: flex;
justify-content: space-between;
align-items: center;
}
.kwa2
{
    width: 20%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}
.foto2
{
    width: 100%;
    height:100%;
}
.extra-thin-div
{
    width: 100%;
    max-width: 1100px;
    height: 1px;
    background-image: linear-gradient(90deg, rgba(217, 217, 217, 0), #d9d9d9 50%, rgba(217, 217, 217, 0));
}
@media (min-height:400px) and (max-height: 800px) {
.o2
{

    height: 70vh;
    max-height: 70dvh;
}
}
@media only screen and (max-width: 768px) {
.o2
{
   
    height: 70vh;
    max-height: 70dvh;
}
.k2{

height: 60vh;
}
.top-bottom
{
 
height: 50vh;
flex-direction: row;
width: 90%;
}
.top2
{

    height: 100%;
    width: 50%;
flex-direction: column;
}
.bottom2
{
   
    height: 100%;
    width: 50%;
    flex-direction: column;
}
.kwa2
{
  
    width: 100%;
    margin: auto;
}
.foto2
{
    width: 100%;
}
}